body {
  box-sizing: border-box;
  background-color: #000;
  margin: 0;
  padding: 0;
}

.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.clock {
  border: 15px solid #1eff00;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
  box-shadow: 5px 5px 300px 5px #1eff00;
}

.clock__center {
  z-index: 3;
  background-color: #1eff00;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  position: absolute;
}

.clock__hand-small {
  z-index: 1;
  transform-origin: bottom;
  background-color: red;
  border-radius: 50%;
  width: 30px;
  height: 140px;
  margin-bottom: 140px;
  animation: 60s linear infinite move;
  position: absolute;
}

.clock__hand-big {
  z-index: 2;
  transform-origin: bottom;
  background-color: #00f;
  border-radius: 50%;
  width: 30px;
  height: 280px;
  margin-bottom: 280px;
  animation: 5s linear infinite move;
  position: absolute;
}

@keyframes move {
  from {
    rotate: none;
  }

  to {
    rotate: 360deg;
  }
}

